<template>
  <div>
    <div class="toolbar">
      <button @click="toggleEditMode">{{ isEditMode ? 'Listo' : 'Editar' }}</button>
    </div>
    <div class="image-container" ref="imageContainer">
      <div
        v-for="img in images"
        :key="img.id"
        class="draggable-image"
        :style="{ width: img.width + 'px', height: img.height + 'px' }"
        :data-id="img.id"
        @click="selectImage(img)"
        @mousedown.prevent="initResize($event, img)"
      >
        <img :src="img.src" :alt="img.name" />
        <div class="resize-handle" @mousedown.stop="initResize($event, img)"></div>
        <!-- Botón para eliminar la imagen -->
        <button v-if="isEditMode" class="delete-button" @click.stop="removeImage(img.id)">Eliminar</button>
      </div>
    </div>

    <div class="external-image-container">
      <div
        v-for="externalImg in externalImages"
        :key="externalImg.id"
        class="external-image"
        @click="duplicateImage(externalImg)"
      >
        <img :src="externalImg.src" :alt="externalImg.name" />
      </div>
    </div>

    <modal v-if="selectedImage" @close="selectedImage = null">
      <h3>{{ selectedImage.name }}</h3>
      <button @click="reserve">Reservar</button>
      <button @click="viewImage">Ver vista</button>
    </modal>
  </div>
</template>

<script>
import interact from 'interactjs';
import Modal from './components/ReservationModal.vue'; 

export default {
  components: {
    Modal,
  },
  data() {
    return {
      isEditMode: true,
      images: [
      ],
      externalImages: [
        { id: 1, name: 'Imagen 1', src: "http://localhost:5000/static/imagen1.png" },
        { id: 2, name: 'Imagen 2', src: "http://localhost:5000/static/imagen2.png" },
        { id: 3, name: 'Imagen 3', src: "http://localhost:5000/static/imagen3.png" },
        { id: 4, name: 'Imagen 4', src: "http://localhost:5000/static/imagen4.png" },
        { id: 5, name: 'Imagen 5', src: "http://localhost:5000/static/baños.png" },
      ],
      selectedImage: null,
    };
  },
  mounted() {
    this.initializeDragAndResize();
  },
  methods: {
    toggleEditMode() {
      this.isEditMode = !this.isEditMode;
    },
    initializeDragAndResize() {
      interact('.draggable-image')
        .draggable({
          inertia: true,
          modifiers: [
            interact.modifiers.restrictRect({
              restriction: 'parent',
            }),
          ],
          autoScroll: true,
          onmove: this.dragMoveListener,
        })
        .resizable({
          edges: { right: true, bottom: true },
          modifiers: [
            interact.modifiers.restrictSize({
              min: { width: 50, height: 50 },
            }),
          ],
          listeners: {
            move: (event) => this.resizeMove(event),
          },
        });
    },
    dragMoveListener(event) {
      const target = event.target;

      // Mantiene la posición actual
      const x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx;
      const y = (parseFloat(target.getAttribute('data-y')) || 0) + event.dy;

      // Traslada el elemento
      target.style.transform = `translate(${x}px, ${y}px)`;

      // Actualiza la posición
      target.setAttribute('data-x', x);
      target.setAttribute('data-y', y);
    },
    selectImage(image) {
      if (!this.isEditMode) {
        this.selectedImage = image;
      }
    },
    reserve() {
      window.location.href = '/reserve';  // Usando el path directamente
    },
    viewImage() {
      alert(`Viendo: ${this.selectedImage.name}`);
      this.selectedImage = null; // Cierra el modal
    },
    duplicateImage(externalImg) {
      const newImg = {
        id: this.images.length + 1,
        name: externalImg.name,
        src: externalImg.src,
        width: 100, // Tamaño inicial
        height: 100,
      };
      this.images.push(newImg);
    },
    resizeMove(event) {
      const target = event.target;
      const img = this.images.find(image => image.id == target.dataset.id);

      if (img) {
        // Actualiza el tamaño de la imagen
        img.width += event.deltaRect.width;
        img.height += event.deltaRect.height;

        // Actualiza el estilo de la imagen
        target.style.width = `${img.width}px`;
        target.style.height = `${img.height}px`;
        
        // Establece el nuevo tamaño de la imagen interna
        const imageElement = target.querySelector('img');
        if (imageElement) {
          imageElement.style.width = `${img.width}px`;
          imageElement.style.height = `${img.height}px`;
        }
      }
    },
    initResize(event, img) {
      if (!event || !img) {
        console.error("El evento o la imagen no están definidos");
        return;
      }
      event.preventDefault(); // Evita que se seleccione el texto
    },
    removeImage(id) {
      this.images = this.images.filter(img => img.id !== id);
    },
  },
};
</script>

<style>
.image-container {
  position: relative;
  width: 100%;
  height: 500px; /* Ajusta según sea necesario */
  border: 1px solid #ccc;
  overflow: hidden;
}

.draggable-image {
  position: absolute;
  cursor: move; /* Cambia el cursor cuando se pasa sobre la imagen */
}

.resize-handle {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 20px;
  height: 20px;
  background: red; /* Cambia el color de la manija de redimensionamiento */
  cursor: nwse-resize; /* Cambia el cursor para indicar redimensionamiento */
}

.delete-button {
  position: absolute;
  top: 5px;
  right: 5px;
  background: red;
  color: white;
  border: none;
  cursor: pointer;
  padding: 5px;
}

.external-image-container {
  display: flex;
  margin-top: 20px; /* Espaciado entre el contenedor de imágenes externas y el plano */
}

.external-image {
  margin: 5px;
  cursor: pointer;
}
</style>
